.UserDialog-Wrapper {
  /*全屏*/
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /*内容居中*/
  display: flex;
  justify-content: center;
  align-items: center;
  background: hsla(0, 0%, 0%, 0.4);
  > .UserDialog {
    border: 1px solid;
    border-radius: 4px;
    min-width: 20em;
    background: white;
    border: 1px solid #aaa;
    box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
    > .signInOrSignUp {
      > nav:first-child {
        padding: 1em;
        > input[type="radio"]:not(:first-child) {
          margin-left: 2em;
        }
      }
      > .panes {
        padding: 0 1em 1em;
      }
    }
    .forgotPassword {
      > h3 {
        padding: 1em;;
      }
      > form{
        padding: 0 1em 1em;
      }
    }
    form {
      > .row {
        display: flex;
        padding: 0.3em 0;
        > label:first-child {
          width: 4em;
        }
      }
      > .row.actions {
        padding-left: 4em;
        display: flex;
        align-items: center;
        > button {
          margin-right: 0.5em;
          font: inherit;
          padding: 0.2em;
        }
        > span {
          color: blue;
          text-decoration: underline;
          cursor: pointer;
        }
      }
    }
  }
}
